@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy|Permanent+Marker|Righteous|Spicy+Rice);
body {
    margin: 0;
    padding: 0;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
 .App {
     text-align: center;
     max-width: 100%;
     min-height: 100vh;
     padding-top: 3rem;
     position: relative;
 }
 
 @media (max-height: 480px) {
     .App {
         padding-top: 1rem;
     }
 }
 
 .App_0 {
     background: var(--background_0);
     color: var(--color_0);
     font-family: var(--font_0);
 }
 
 .App_1 {
     background: var(--background_1);
     color: var(--color_1);
     font-family: var(--font_1);
 }
 
 .App_2 {
     background: var(--background_2);
     color: var(--color_2);
     font-family: var(--font_2);
 }
 
 .App_3 {
     background: var(--background_3);
     color: var(--color_3);
     font-family: var(--font_3);
 }
 
 .container {
     width: 600px;
     max-width: 100%;
     margin: auto;
 }
.button {
    width: var(--button_size);
    height: var(--button_size);
    background: inherit;
    margin: 5px;
    font-family: inherit;
    color: inherit;
}

.buttonSpacebar {
    width: 500px;
}
.pads {
    max-width: 100%;
    padding: 30px;
}

@media (max-height: 480px) {
    .pads {
        padding: 10px;
    }
}
.display {
    padding: 0px;
}

.toggle_button {
    padding: 5px;
    margin: 10px;
    background: inherit;
    color: inherit;
    font-family: monospace;
}
@media (max-height: 480px) {
    .title {
        margin: 0;
        padding: 0;
    }
    .subtitle {
        display: none;
    }
}
:root {
    /*color schemes*/
    --background_0: #512DA8;
    --color_0: gold;
    --background_1: #00796B;
    --color_1: white;
    --background_2: #1976D2;
    --color_2: white;
    --background_3: #795548;
    --color_3: white;
    /*font families*/
    --font_0: 'Righteous', cursive;
    --font_1: 'Permanent Marker', cursive;
    --font_2: 'Luckiest Guy', cursive;
    --font_3: 'Spicy Rice', cursive;
    /*sizes*/
    --button_size: 60px;
}
* {
    margin: 0;
    padding: 0;
    max-width: 100%;
}

html {
    font-size: 17px;
    letter-spacing: 2px;
}
.footer {
    margin-top: 4rem;
    width: 100%;
    font-size: 0.7rem;
}
